<template>
  <v-data-table
    :headers="headers"
    :items="convocatorias"
    :items-per-page="5"
    class="elevation-1"
    >
    <template v-slot:item.action="{ item }">
      <v-btn small @click="newConvProp(item)" color="primary" v-if="!item.referencia">
        <v-icon dark small left>plus</v-icon>Generar propuesta
      </v-btn>
      <v-btn small @click="editConvProp(item)" color="primary" v-if="item.referencia">
        <v-icon dark small left>edit</v-icon>Editar propuesta
      </v-btn>
    </template>
    <template v-slot:item.tipo_contratacion="{ item }">
      {{getNameTipoContratacion(item.tipo_contratacion)}}
    </template>
    <template v-slot:item.fecha_creacion="{ item }">
      {{item.fecha_creacion}}
    </template>
    <template v-slot:item.fecha_cierre="{ item }">
      {{item.fecha_cierre}}
    </template>
  </v-data-table>
</template>
<script>
import { propConv } from '@/servicios/propuestaConvocatoria'
import { getTipoContratacionName } from '@/servicios/utilities.js'

import moment from 'moment';
export default {
  props: ['convocatorias'],
  data () {
    return {
      headers: [
        { text: 'Acción', value: 'action', align: 'center' },
        { text: 'Referencia', value: 'referencia', align: 'center' },
        { text: 'Titulo del Proceso', value: 'titulo', align: 'center' },
        { text: 'Termino Referencia', value: 'id_termref', align: 'center' },
        { text: 'Tipo Contratación', value: 'tipo_contratacion', align: 'center' },
        { text: 'Apertura', value: 'fecha_apertura', align: 'center'},
        { text: 'Cierre', value: 'fecha_cierre', align: 'center'},
        /* { text: 'Creado', value: 'fecha_creacion', align: 'center'}, */
        { text: 'Estado', value: 'estado', align: 'center' }
      ]
    }
  },
  filters: {
    dateFormat (value) {
      return moment(String(value)).locale('es').format('DD-MMM YYYY HH:MM').toUpperCase()
    }
  },
  mounted () {
    this.getTerminos()
  },
  methods: {
    async getTerminos () {
      try {
        const { data } = await propConv.getPropuestasConvocatoria()
        this.$emit('setConvocatorias', data)
      } catch (error) {
        console.log(error)
      }
    },
    newConvProp (item) {
      this.$emit('showConvPropForm', item )
    },
    editConvProp (item) {
      this.$emit('showConvPropForm', item )
    },
    getNameTipoContratacion (tipo_contratacion) {
      return getTipoContratacionName(tipo_contratacion)
    }
  },
}
</script>
