<template>
  <v-container>
    <h1>Publicación de convocatorias</h1>
    <ConvocatoriaList
      :convocatorias="convocatorias"
      @showConvPropForm = "showConvPropForm($event)"
      @setConvocatorias = "setConvocatorias($event)"
      />
    <v-dialog v-model="dialogNewCP" width="60%" v-if="selPropCont">
      <FormPropCont
        :selPropCont="selPropCont"
        @saved="saved"
      />
    </v-dialog>
  </v-container>
</template>
<script>
import ConvocatoriaList from '@/components/propConvocatoria/ConvocatoriaList'
import Vue from 'vue'
export default {
  components: {
    ConvocatoriaList,
    FormPropCont: () => import('@/components/propConvocatoria/FormPropCont.vue')
  },
  data () {
    return {
      convocatorias: [],
      dialogNewCP: false,
      selPropCont: null
    }
  },
  methods: {
    setConvocatorias (data) {
      const dataList = data.data
      console.log(dataList)
      this.convocatorias = dataList
    },
    showConvPropForm (event) {
      console.log(event)
      this.selPropCont = event
      this.dialogNewCP = true
    },
    saved (propconv) {
      console.log(propconv)
      const conv = this.convocatorias.find(item => item.id === propconv.id)
      if (conv) {
        conv.referencia = propconv.referencia
        conv.titulo = propconv.titulo
        conv.descripcion = propconv.descripcion
        conv.fecha_cierre = propconv.fecha_cierre
        conv.fecha_apertura = propconv.fecha_apertura
        conv.estado = propconv.estado
        conv.esPropConv = 1
      }
      Vue.set(this.convocatorias, this.convocatorias)
      this.dialogNewCP = false
      this.selPropCont = null
    },

    getSiglasTipoContrata (tipo_contratacion) {
      switch (tipo_contratacion) {
        case 'dir_personal':
            return 'CPN';
            break;
        case 'dir_bien':
            return 'CPJ';
            break;
        case 'dir_juridica':
            return 'CPJ';
            break;
        case 'lista_corta':
            return 'LC';
            break;
        case 'conv_publica':
            return 'CD';
            break;
        default:
            return 'Error';
            break;
      }
      return 'Error';
    }
  }
}
</script>
