import axios from 'axios'
import configService from './config'

const propConv = {
    getPropuestasConvocatoria: params => axios.get(configService.apiUrl+ '/propuestaConvocatoria', { params }),
    savePropuestaConvocatoria: params => axios.post(configService.apiUrl+ '/propuestaConvocatoria', params, { headers: { 'Content-Type': 'multipart/form-data' } }),
    editPropuestaConvocatoria: (id, params) => axios.put(configService.apiUrl+ '/propuestaConvocatoria/' + id, params, { headers: { 'Content-Type': 'multipart/form-data' } })
}

export {
    propConv
}